import { Injectable } from '@angular/core';
import { LightNation } from '../common/lightNation';
import { AppVersion } from '@ionic-native/app-version';

/**
 * light
 * @export
 * @class LightOpen
 * @extends {LightNation}
 */
@Injectable()
export class LightAppVersion extends LightNation {

    /**
     * :npm install --save @ionic-native/app-version
     * :ionic cordova plugin add cordova-plugin-app-version
     * App
     * @memberof LightAppVersion
     */
    constructor(public appVersion: AppVersion) {
        super();
    }

    /**
     * App
     * 
     * @returns {string} 
     * @memberof LightAppVersion
     */
    getAppName(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("aaa");
        if (!this._isWeb()) {
            result = this.appVersion.getAppName();
        }
        return result;
    }


    /**
     * 
     * @returns {string} 
     * @memberof LightAppVersion
     */
    getPackageName(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("aaa");
        if (!this._isWeb()) {
            result = this.appVersion.getPackageName();
        }
        return result;
    }

    /**
     * 
     * @returns {Promise<any>} 
     * @memberof LightAppVersion
     */
    getVersionCode(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("0.0.0");
        if (!this._isWeb()) {
            result = this.appVersion.getVersionCode();
        }
        return result;
    }
    
    /**
     * 
     * @returns {Promise<any>} 
     * @memberof LightAppVersion
     */
    getVersionNumber(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("0.0.0");
        if (!this._isWeb()) {
            result = this.appVersion.getVersionNumber();
        }
        console.log(result);
        return result;
        
    }
}


