///
export class LightStringRegexUtil {

    /**
     * 
     * 111
     * @static
     * @param {string}  
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkMobileNumber(mobileText: string): boolean {
        var result = false;
        var re = /^1\d{10}$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

    /**
     * 
     * +034
     * 78
     * -
     * 01088888888,010-88888888,0955-7777777
     * @static
     * @param {string} mobileText 
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkPhone(mobileText: string): boolean {
        var result = false;
        var re = /^0\d{2,3}-?\d{7,8}$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

    /**
     * 
     * 
     * @static
     * @param {string} mobileText 
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkEmail(mobileText: string): boolean {
        var result = false;
        var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

}


