import { Injectable } from '@angular/core';
import { Platform, ToastController } from 'ionic-angular';
import { HotCodePush } from '@ionic-native/hot-code-push';
import { LightNation } from '../common/lightNation';
import { LightProviders } from '../../light-providers/light-providers.module';

@Injectable()
export class LightHotCodePush extends LightNation {

    /**
     * nation: npm install --save @ionic-native/hot-code-push
     * nationCLI: npm install -g cordova-hot-code-push-cli
     * ionic: ionic cordova plugin add cordova-hot-code-push-plugin
     * Creates an instance of LightHotCodePush.
     * @param {HotCodePush} hotCodePush 
     * @memberof LightHotCodePush
     */

    constructor(
        public platform: Platform,
        public hotCodePush: HotCodePush,
        public toastCtrl: ToastController
    ) {
        super();
    }

    /** 
     * 
     * 
     * 
     * 
     *      
     *      
     *      
     *      
     * 
    */

    //
    public initCodePush(): void {

        //
        document.addEventListener("chcp_beforeInstall", event => {

            console.log("");

            alert('');

        }, false);

        //
        document.addEventListener("chcp_updateInstalled", event => {

            console.log("");

            alert('');

        }, false);

        //
        document.addEventListener("chcp_updateInstallFailed", event => {

            console.log("");

            alert('');

        }, false);

        //
        document.addEventListener("chcp_nothingToInstall", event => {

            console.log("");

            alert('');

        }, false);

    }


    public messageTip(content: string) {
        const toast = this.toastCtrl.create({
            message: content,
            duration: 3000,
            position: 'top'
        });
        toast.present();
    }


    // chcp_updateIsReadyToInstall - 
    // chcp_updateLoadFailed - 
    // chcp_nothingToUpdate - 
    // chcp_beforeInstall - 
    // chcp_updateInstalled - 
    // chcp_updateInstallFailed - 
    // chcp_nothingToInstall - 
    // chcp_beforeAssetsInstalledOnExternalStorage - 
    // chcp_assetsInstalledOnExternalStorage - bundleWeb
    // chcp_assetsInstallationError - bundle - 
    // document.addEventListener('chcp_updateIsReadyToInstall', this.onUpdateReady, false);

    /**
     * requestApplicationUpdate(message)
     * App StoreGoogle Play
     *  string 
     *  Promise<any>
     */
    requestApplicationUpdate(message: any) {
        return this.hotCodePush.requestApplicationUpdate(message);
    }

    /**
     * fetchUpdate(options)
     * 
     *  HotCodePushRequestOptions  - config.xml
     *  Promise < any > 
     */
    fetchUpdate(options: any) {
        return this.hotCodePush.fetchUpdate(options);
    }

    /**
     * installUpdate()
     * 
     *  Promise<any>
     */
    installUpdate() {
        return this.hotCodePush.installUpdate();
    }

    /**
     * isUpdateAvailableForInstallation()
     * 
     *  Promise<HotCodePushUpdate>
     */
    isUpdateAvailableForInstallation() {
        return this.hotCodePush.isUpdateAvailableForInstallation();
    }

    /**
     * getVersionInfo()
     * 
     *  Promise<HotCodePushVersion>
     */
    getVersionInfo() {
        return this.hotCodePush.getVersionInfo();
    }

    /**
     * onUpdateIsReadyToInstall()
     * 
     *  Observable<HotCodePushEventData>
     */
    onUpdateIsReadyToInstall() {
        return this.hotCodePush.onUpdateIsReadyToInstall().subscribe();
    }

    /**
     * onUpdateLoadFailed()
     * 
     *  Observable<HotCodePushEventData>
     */
    onUpdateLoadFailed() {
        return this.hotCodePush.onUpdateLoadFailed();
    }

    /**
     * onNothingToUpdate()
     * 
     *  Observable<HotCodePushEventData>
     */
    onNothingToUpdate() {
        return this.hotCodePush.onNothingToUpdate();
    }

    /**
     * onBeforeInstall()
     * 
     *  Observable<HotCodePushEventData>
     */
    onBeforeInstall() {
        return this.hotCodePush.onBeforeInstall();
    }

    /**
     * onUpdateInstalled()
     * 
     *  Observable<HotCodePushEventData>
     */
    onUpdateInstalled() {
        return this.hotCodePush.onUpdateInstalled();
    }

    /**
     * onUpdateInstallFailed()
     * 
     *  Observable<HotCodePushEventData>
     */
    onUpdateInstallFailed() {
        return this.hotCodePush.onUpdateInstallFailed();
    }

    /**
     * onNothingToInstall()
     * 
     *  Observable<HotCodePushEventData>
     */
    onNothingToInstall() {
        return this.hotCodePush.onNothingToInstall();
    }

    /**
     * onBeforeAssetsInstalledOnExternalStorage()
     * 
     *  Observable<HotCodePushEventData>
     */
    onBeforeAssetsInstalledOnExternalStorage() {
        return this.hotCodePush.onBeforeAssetsInstalledOnExternalStorage();
    }

    /**
     * onAssetsInstalledOnExternalStorage()
     * bundleWeb
     *  Observable<HotCodePushEventData>
     */
    onAssetsInstalledOnExternalStorage() {
        return this.hotCodePush.onAssetsInstalledOnExternalStorage();
    }

    /**
     * onAssetsInstallationError()
     * bundle - 
     *  Observable<HotCodePushEventData>
     */
    onAssetsInstallationError() {
        return this.hotCodePush.onAssetsInstallationError();
    }

}


