import { NavController } from 'ionic-angular';
import { LightProviders } from '../../light-providers/light-providers.module';
import { LightJsonUtil, LightStringUtil } from '../../light-util/light-util.module';
import { LightAppVersion, LightFileTransfer, LightFileOpen, LightFileType } from '../../light-nation/light-nation.module';

/**
 * 
 * 
 * @export
 * @class LightLogin
 */
export class LightApp {

    //
    updateFlag: boolean = false;

    //
    appVersion: string = "0.0.2";

    ///Url
    upgradeURL: string = "";

    /**
     * 
     * @param {NavController} navCtrl 
     * @param {LightProviders} lightProviders 
     * @memberof LightLogin
     */
    constructor(
        public navCtrl: NavController,
        public lightProviders: LightProviders,
    ) {

    }

    /**
     * 
     */
    autoLoginCheck(): Promise<any> {
        return this.lightProviders.lightUser.autoLoginCheck();
    }

    /**
     * 
     * 
     * @param {string} userName  
     * @param {string} userPassword 
     * @param {*} page 
     * @memberof LightLogin
     */
    loginToPage(userName: string, userPassword: string, page: any): void {
        if (!!userName && !!userPassword) {
            this.lightProviders.lightUser.userLogin(userName, userPassword).then(value => {
                if (!!page) {
                    this.lightProviders.lightNav.setRootPage(page);
                    this.lightProviders.lightNav.push(this.navCtrl, page);
                }
            }).catch(errText => {
                if (LightStringUtil.contain(errText, "")) {
                    this.lightProviders.lightPop.message(errText);
                } else {
                    this.lightProviders.lightPop.message("");
                }
            });
        } else {
            this.lightProviders.lightPop.message("");
        }
    }

    /**
     * APP
     * @param {LightAppVersion} lightAppVersion 
     * @param {LightFileTransfer} lightFileTransfer 
     * @param {LightFileOpen} lightFileOpen 
     * @memberof LightApp
     */
    UpdataAppInfo(lightAppVersion: LightAppVersion): void {
        //
        var that = this;
        lightAppVersion.getVersionNumber().then(versionData => {
            that.appVersion = versionData;
            console.log(" " + versionData);
            that.lightProviders.lightHttp.get("Upgrade/UpgradeApp", { version: that.appVersion }).then(value => {
                var data = LightJsonUtil.toObject(value) as any;
                if (!!data.VersionNumber) {
                    if (that.appVersion == data.VersionNumber) {
                        //that.lightProviders.lightPop.errMessage("");
                    }
                    else {
                        //that.appVersion = data;
                        that.updateFlag = true;
                        that.upgradeURL = data.UpgradeURL;
                    }
                }
                else {
                    that.lightProviders.lightPop.errMessage("");
                }

            })
        })
    }

    /**
     * APP
     * @param {LightFileTransfer} lightFileTransfer 
     * @param {LightFileOpen} lightFileOpen 
     * @memberof LightApp
     */
    UpdataApp(lightFileTransfer: LightFileTransfer, lightFileOpen: LightFileOpen, listener: (event: ProgressEvent) => any = null): void {
        if (this.updateFlag) {
            let alert = this.lightProviders.lightPop.alertCtrl.create({
                title: '',
                message: '',
                buttons: [{
                    text: '',
                    role: 'cancel',
                    handler: () => {
                        //console.log('');
                    }
                }, {
                    text: '',
                    handler: () => {
                        //console.log('');
                        this._downUpdataApp(this.upgradeURL, lightFileTransfer, lightFileOpen, listener);
                    }
                }]
            });
            alert.present();
        }
    }

    //
    private _downUpdataApp(downUrl: string, lightFileTransfer: LightFileTransfer, lightFileOpen: LightFileOpen, listener: (event: ProgressEvent) => any = null) {
        if (!!this.updateFlag && !!downUrl) {
            let appFileName = "LightOA_" + this.appVersion + ".apk";
            //app 
            lightFileTransfer.download(downUrl, appFileName, LightFileType.RootFile, listener).then((value) => {
                //console.log("");
                lightFileOpen.openFile(value).then(data => {
                    console.log('apk')
                })
                    .catch(err => {
                        this.lightProviders.popMessage("apk" + err);
                        //console.log('apk')
                    });
            }).catch(errText => {
                this.lightProviders.popMessage("!" + errText);
            });
        }
    }

    /**
     * 
     */
    exitApp(): void {
        this.lightProviders.lightExit.appExit();
    }

    /**
     * 
     */
    logoutPage(page: any) {
        console.log(page);
        this.lightProviders.lightPop.confirm("").then((resultText) => {
            console.log(resultText);
            this.lightProviders.lightUser.userExit();
            this.navCtrl.push(page);
        }).catch((errText) => {
            console.log(errText);
        });
    }

}


