import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';

//
@Injectable()
export class LightPlatform {

    /**
     * 
     * @param {Platform} platform 
     * 
     * @memberof LightPlatform
     */
    constructor(
        public platform: Platform
    ) {

    }

    /**
     * IOS
     * @returns {boolean} 
     * @memberof LightPlatform
     */
    isIOS(): boolean {
        var result: boolean = false;
        if (this.platform.is('ios')) {
            result = true;
        }
        return result;
    }

    /**
     * Android
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isIPad(): boolean {
        var result: boolean = false;
        if (this.platform.is('ipad')) {
            result = true;
        }
        return result;
    }

    /**
     * Android
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isIPhone(): boolean {
        var result: boolean = false;
        if (this.platform.is('iphone')) {
            result = true;
        }
        return result;
    }

    /**
     * Android
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isAndroid(): boolean {
        var result: boolean = false;
        if (this.platform.is('android')) {
            result = true;
        }
        return result;
    }

    /**
     * Window
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isWindows(): boolean {
        var result: boolean = false;
        //console.log(this.platform.platforms);
        if (this.platform.is('windows')) {
            result = true;
        }
        return result;
    }

    /**
     * web
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isMobileWeb(): boolean {
        var result: boolean = false;
        if (this.platform.is('mobileweb')) {
            result = true;
        }
        return result;
    }

}



